<template>
    <div style="height:100%" >
        <Header></Header>
        <div class='container'>
            <div class="qrcode-container">
                <canvas id="QRCode"></canvas>
            </div>
            <span></span>
            <!-- <span class="date-span">{{getFiveMinDate()}}</span> -->
        </div>

    </div>

</template>
<style scoped>
    .container{
        padding-top: 40px;
        display: flex;
        justify-content: center;
        align-items: center;
        background: #e9e9e9;
        height: 100%;
        flex-direction: column;
    }
    .qrcode-container{
        display: flex;
        justify-content: center;
        align-items: center;
        background: white;
        width: 100%;
    }
    #QRCode{
        height: 300px !important;
        width: 300px !important;
    }
    .date-span{
        margin-top: 10px;
    }
</style>
<script>
import QRCode from 'qrcode' // 
import Header from '../../components/LeftHeader'
export default {
  data () {
    return {
      QRCodeMsg: '' // 
    }
  },
  components: {
    Header
  },
  mounted () {
    this.init()
  },
  methods: {
    init () {
      this.QRCodeMsg = window.location.href// URL
    },
    // 
    getFiveMinDate () {
      var date = new Date() // 1. js
      var min = date.getMinutes()  // 2. 
      date.setMinutes(min + 10)  // 3. +10+10date
      var y = date.getFullYear()
      var m = (date.getMonth() + 1) < 10 ? ("0" + (date.getMonth() + 1)) : (date.getMonth() + 1);
      var d = date.getDate() < 10 ? ("0" + date.getDate()) : date.getDate();
      var h = date.getHours() < 10 ? ('0' + date.getHours()) : date.getHours()
      var f = date.getMinutes() < 10 ? ('0' + date.getMinutes()) : date.getMinutes()
      var s = date.getSeconds() < 10 ? ('0' + date.getseconds()) : date.getSeconds()
      var formatdate = y+'-'+m+'-'+d + " " + h + ":" + f + ":" + s;
      return formatdate // 10yyyy-mm-dd h:f:s
    }
  },
  watch: {
    // 
    QRCodeMsg (val) {
      // canvas
      var msg = document.getElementById('QRCode')
      // valmsgcanvas
      QRCode.toCanvas(msg, val, function (error) {
        console.log(error)
      })
    }
  }
}
</script>



